.home__image{
    width: 100%;
    z-index: -1;
    margin-bottom: -150px;
    mask-image: linear-gradient(to bottom ,rgb(0,0,0,1),rgb(0,0,0,0));
    
}


.home{
    /* the big container where product and everything is there */
    /* isko hamne limit kr diya ki ek width tk rahega ye */
    display: flex;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    max-width: 1500px;
}

.home__row{
    display: flex;
    z-index: 1;
    margin-left: 5px;
    margin-right: 5px;
}

