.payment{
    background-color: white;
}

.payment__container{
    text-align: center;
    padding: 10px;
    font-weight: 440;
    background-color: rgb(234, 237, 237);
    border-bottom: 1px solid lightgray;
}

.payment__container > h1 a{
    text-decoration: none;
}

.payment__section{
    display: flex;
    padding: 20px; 
    margin: 0;
    border-bottom: 1px solid lightgray;
}

.payment__title{
    flex: 0.2;
    /* 20% de rahe hai isko  */
}

.payment__address, .payment__items, .payment__details{
    flex: 0.8; 
}

